import React, { forwardRef, useImperativeHandle, useRef, useState } from 'react'
import reactDom from 'react-dom'
import useAuth from '../core/useAuth'
import useFormValidate from '../core/useFormValidate'

export default forwardRef(function Login(props, ref) {

    let [formError, setFormError] = useState()




    let { handleLogin } = useAuth()

    let { form, error, inputChange, check } = useFormValidate({
        username: '',
        password: ''
    }, {
        rule: {
            username: {
                required: true,
                pattern: "email"
            },
            password: {
                required: true,

            }
        },
        message: {
            username: {
                required: true,
                pattern: ""
            }
        },

    })





    let divRef = useRef()

    useImperativeHandle(ref, () => {
        return {
            open,
            close
        }
    }, [])



    function open() {
        divRef.current.style.display = 'flex'
    }

    function close() {
        divRef.current.style.display = 'none'
    }

    async function _login() {
        let error = check();

        if (Object.keys(error).length === 0) {
            let res = await handleLogin(form)
            if (res?.error) {
                setFormError(res.error)
            }
            else {
                close()
            }


        }
    }



    return reactDom.createPortal(
        <div className="popup-form popup-login" id='popupLogin' ref={divRef} style={{ display: 'none' }}>
            <div className="wrap">
                {/* login-form */}
                <div className="ct_login" style={{ display: 'block' }}>
                    <h2 className="title">Đăng nhập</h2>
                    {
                        formError ? <h2 style={{ color: 'red', textAlign: 'center', marginBottom: 20 }} >{formError}</h2> : ""
                    }
                    <div style={{ position: 'relative' }}>
                        <label>
                            <input name='username' value={form.username} onChange={inputChange} type="text" placeholder="Email / Số điện thoại" />
                        </label>
                        {error.username && <p className=" errorLogin">{error.username}</p>}
                    </div>

                    <div style={{ position: 'relative' }}>
                        <label>
                            <input name='password' value={form.password} onChange={inputChange} type="password" placeholder="Mật khẩu" />
                        </label>
                        {error.password && <p className=" errorLogin">{error.password}</p>}
                    </div>


                    <div className="remember">
                        <label className="btn-remember">
                            <div>
                                <input type="checkbox" />
                            </div>
                            <p>Nhớ mật khẩu</p>
                        </label>
                        <a href="#" className="forget">Quên mật khẩu?</a>
                    </div>
                    <div className="btn rect main btn-login" onClick={_login}>đăng nhập</div>
                    <div className="text-register" style={{}}>
                        <strong>hoặc đăng ký bằng</strong>
                    </div>
                    <div>
                        <div className="btn btn-icon rect white btn-google">
                            <img src="/img/google.svg" alt />
          Google
        </div>
                    </div>
                    <div className="close">
                        <img src="/img/close-icon.png" alt />
                    </div>
                </div>
                {/* email form */}
                <div className="ct_email">
                    <h2 className="title">Đặt lại mật khẩu</h2>
                    <input type="text" placeholder="Email" />
                    <div className="btn rect main btn-next">Tiếp theo</div>
                    <div className="back" />
                    <div className="close">
                        <img src="/img/close-icon.png" alt />
                    </div>
                </div>
            </div>
        </div>,
        document.getElementById('root2')
    )
})

